body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.App {
  background: url(https://www.xmple.com/wallpaper/white-yellow-gradient-linear-5120x2880-c2-fff0f5-ffdab9-a-75-f-14.svg);
  background-size: cover;
  background-position: center;
  height: 200vh;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

.change {
  text-decoration: line-through;
  font-style: italic;
  font-weight: 500;
}
